/**
 * Á©∫ÊäïÊ±†ÂèÇ‰∏éË°®ÂçïÁªÑ‰ª∂
 * Áî®Êà∑ÂèÇ‰∏éÂ∞èÁ∫¢Ëä±Á©∫ÊäïÊ±†ÁöÑË°®Âçï
 */

import React, { useState, useEffect } from 'react';
import { AirdropParticipationFormProps } from '@/types/components';
import { NumberInput } from '@/components/ui/SecureInput';
import { useWallet } from '@/contexts/WalletContext';
import { secureHttpClient } from '@/lib/secureHttpClient';

export default function AirdropParticipationForm({
  pool,
  userFlowerBalance,
  onSubmit,
  isSubmitting = false,
  className,
  ...props
}: AirdropParticipationFormProps) {
  const { wallet } = useWallet();
  const [entryAmount, setEntryAmount] = useState<string>('');
  const [isValid, setIsValid] = useState<boolean>(false);
  const [error, setError] = useState<string>('');

  // È™åËØÅËæìÂÖ•
  useEffect(() => {
    const amount = parseInt(entryAmount);
    
    if (!entryAmount || isNaN(amount)) {
      setIsValid(false);
      setError('');
      return;
    }

    if (amount < pool.entryFee) {
      setIsValid(false);
      setError(`ÊúÄÂ∞ëÈúÄË¶Å ${pool.entryFee} Â∞èÁ∫¢Ëä±`);
      return;
    }

    if (amount > userFlowerBalance) {
      setIsValid(false);
      setError('Â∞èÁ∫¢Ëä±‰ΩôÈ¢ù‰∏çË∂≥');
      return;
    }

    setIsValid(true);
    setError('');
  }, [entryAmount, pool.entryFee, userFlowerBalance]);

  // ËÆæÁΩÆÊúÄÂ∞èÈáëÈ¢ù
  const handleMinAmount = () => {
    setEntryAmount(pool.entryFee.toString());
  };

  // ËÆæÁΩÆÊúÄÂ§ßÈáëÈ¢ù
  const handleMaxAmount = () => {
    setEntryAmount(userFlowerBalance.toString());
  };

  // Êèê‰∫§Ë°®Âçï
  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!isValid || isSubmitting) return;

    try {
      await onSubmit(parseInt(entryAmount));
      setEntryAmount('');
    } catch (error) {
      console.error('ÂèÇ‰∏éÁ©∫ÊäïÊ±†Â§±Ë¥•:', error);
    }
  };

  // ËÆ°ÁÆóÈ¢ÑÊúüÂ•ñÂä±ÊØî‰æã
  const calculateRewardRatio = (): string => {
    if (!entryAmount || isNaN(parseInt(entryAmount))) return '0';
    
    const amount = parseInt(entryAmount);
    const totalPool = pool.currentParticipants * pool.entryFee + amount;
    const ratio = (amount / totalPool) * 100;
    
    return ratio.toFixed(2);
  };

  // ËÆ°ÁÆóÈ¢ÑÊúüÂ•ñÂä±
  const calculateExpectedReward = (): string => {
    if (!entryAmount || isNaN(parseInt(entryAmount))) return '0';
    
    const ratio = parseFloat(calculateRewardRatio()) / 100;
    const expectedReward = parseFloat(pool.totalReward) * ratio;
    
    return expectedReward.toFixed(6);
  };

  return (
    <div className={`bg-white rounded-lg shadow-md p-6 ${className || ''}`} {...props}>
      <h3 className="text-lg font-semibold mb-4">ÂèÇ‰∏éÁ©∫ÊäïÊ±†</h3>
      
      {/* Á©∫ÊäïÊ±†‰ø°ÊÅØ */}
      <div className="bg-gray-50 rounded-lg p-4 mb-6">
        <h4 className="font-medium mb-2">{pool.name}</h4>
        <div className="grid grid-cols-2 gap-4 text-sm">
          <div>
            <span className="text-gray-600">ÊÄªÂ•ñÂä±:</span>
            <span className="ml-2 font-medium">{pool.totalReward} {pool.tokenType}</span>
          </div>
          <div>
            <span className="text-gray-600">ÊúÄ‰ΩéÂÖ•Âú∫:</span>
            <span className="ml-2 font-medium">üå∫ {pool.entryFee}</span>
          </div>
          <div>
            <span className="text-gray-600">ÂèÇ‰∏é‰∫∫Êï∞:</span>
            <span className="ml-2 font-medium">{pool.currentParticipants}/{pool.maxParticipants}</span>
          </div>
          <div>
            <span className="text-gray-600">ÁªìÊùüÊó∂Èó¥:</span>
            <span className="ml-2 font-medium">
              {new Date(pool.endDate).toLocaleDateString()}
            </span>
          </div>
        </div>
      </div>

      {/* Áî®Êà∑‰ΩôÈ¢ù */}
      <div className="mb-4">
        <div className="flex justify-between items-center">
          <span className="text-sm text-gray-600">ÊÇ®ÁöÑÂ∞èÁ∫¢Ëä±‰ΩôÈ¢ù:</span>
          <span className="font-medium">üå∫ {userFlowerBalance.toLocaleString()}</span>
        </div>
      </div>

      {/* ÂèÇ‰∏éË°®Âçï */}
      <form onSubmit={handleSubmit}>
        <div className="mb-4">
          <label className="block text-sm font-medium text-gray-700 mb-2">
            ÊäïÂÖ•Â∞èÁ∫¢Ëä±Êï∞Èáè
          </label>
          <div className="relative">
            <NumberInput
              value={entryAmount}
              onChange={(e) => setEntryAmount(e.target.value)}
              placeholder={`ÊúÄÂ∞ë ${pool.entryFee} Â∞èÁ∫¢Ëä±`}
              minValue={pool.entryFee}
              maxValue={userFlowerBalance}
              className="w-full"
              disabled={isSubmitting}
            />
            <div className="absolute right-3 top-1/2 transform -translate-y-1/2">
              <span className="text-red-500">üå∫</span>
            </div>
          </div>
          
          {/* Âø´ÈÄüÈÄâÊã©ÊåâÈíÆ */}
          <div className="flex space-x-2 mt-2">
            <button
              type="button"
              onClick={handleMinAmount}
              className="px-3 py-1 text-xs bg-gray-100 hover:bg-gray-200 rounded"
              disabled={isSubmitting}
            >
              ÊúÄÂ∞è
            </button>
            <button
              type="button"
              onClick={handleMaxAmount}
              className="px-3 py-1 text-xs bg-gray-100 hover:bg-gray-200 rounded"
              disabled={isSubmitting}
            >
              ÂÖ®ÈÉ®
            </button>
          </div>
          
          {error && (
            <p className="mt-1 text-sm text-red-600">{error}</p>
          )}
        </div>

        {/* È¢ÑÊúüÂ•ñÂä± */}
        {entryAmount && isValid && (
          <div className="mb-4 p-3 bg-blue-50 rounded-lg">
            <h5 className="text-sm font-medium text-blue-800 mb-2">È¢ÑÊúüÂ•ñÂä±</h5>
            <div className="space-y-1 text-sm text-blue-700">
              <div className="flex justify-between">
                <span>Â•ñÂä±ÊØî‰æã:</span>
                <span>{calculateRewardRatio()}%</span>
              </div>
              <div className="flex justify-between">
                <span>È¢ÑÊúüËé∑Âæó:</span>
                <span className="font-medium">
                  {calculateExpectedReward()} {pool.tokenType}
                </span>
              </div>
            </div>
            <p className="text-xs text-blue-600 mt-2">
              * ÂÆûÈôÖÂ•ñÂä±Ê†πÊçÆÊúÄÁªàÂèÇ‰∏éÊÄªÈ¢ùÊåâÊØî‰æãÂàÜÈÖç
            </p>
          </div>
        )}

        {/* Êèê‰∫§ÊåâÈíÆ */}
        <button
          type="submit"
          disabled={!isValid || isSubmitting || !wallet.isConnected}
          className={`w-full py-3 px-4 rounded-lg font-medium ${
            !isValid || isSubmitting || !wallet.isConnected
              ? 'bg-gray-300 text-gray-500 cursor-not-allowed'
              : 'bg-blue-500 text-white hover:bg-blue-600'
          }`}
        >
          {isSubmitting ? 'ÂèÇ‰∏é‰∏≠...' : wallet.isConnected ? 'Á°ÆËÆ§ÂèÇ‰∏é' : 'ËØ∑ËøûÊé•Èí±ÂåÖ'}
        </button>
      </form>

      {/* È£éÈô©ÊèêÁ§∫ */}
      <div className="mt-4 p-3 bg-yellow-50 rounded-lg">
        <h5 className="text-sm font-medium text-yellow-800 mb-1">È£éÈô©ÊèêÁ§∫</h5>
        <ul className="text-xs text-yellow-700 space-y-1">
          <li>‚Ä¢ ÂèÇ‰∏éÂêéÊó†Ê≥ïÈÄÄÂá∫ÔºåËØ∑Ë∞®ÊÖéÊäïÂÖ•</li>
          <li>‚Ä¢ Â•ñÂä±ÊåâÊúÄÁªàÂèÇ‰∏éÊØî‰æãÂàÜÈÖç</li>
          <li>‚Ä¢ ÂàÜÂèëÊó∂Èó¥‰∏∫Á©∫ÊäïÊ±†ÁªìÊùüÂêé</li>
          <li>‚Ä¢ ËØ∑Á°Æ‰øùÈí±ÂåÖÂú∞ÂùÄÊ≠£Á°Æ</li>
        </ul>
      </div>
    </div>
  );
}
